#btns {
  position: fixed;
  top: 50%;
  right: 50px;
  transform: translateY(-50%);

  li {
    width: 10px;
    height: 10px;
    border-radius: 5px;
    background: #fff;
    margin-bottom: 20px;
    cursor: pointer;
    transition: 0.5s;

    &.on {
      height: 80px; 
      background: #111;
    }
  }
}