* {
  margin: 0px; padding: 0px; box-sizing: border-box;
}
ul, ol {
  list-style: none;
}
a {
  text-decoration: none;
  outline: 0;
}
table {
  border-collapse: collapse;
  border: none;

  th, td {
    border: none;
  }
}
fieldset {
  border: none;
}

body {
  background: #efefef;
}
.h {
  position: absolute; top: -99999px; opacity: 0;
}
label, input {
  vertical-align: middle;
}
